// all banner types on site

.title {

  // custom content headline
  font-family: $font-title-01;
  line-height: 1.1em;
  &--xxxl   { font-size: $s8; }
  &--xxl    { font-size: $s7; }
  &--xl     { font-size: $s6; }
  &--lg     { font-size: $s5; }
  &--md     { font-size: $s4; }
  &--sm     { font-size: $s3; }
  &--xs     { font-size: $s2; }
  &--xxs    { font-size: $s1; }
  &--xxxs   { font-size: $s0; }
  @include bp($lg){
    &--xxxl   { font-size: $s9; }
    &--xxl    { font-size: $s8; }
    &--xl     { font-size: $s7; }
    &--lg     { font-size: $s6; }
    &--md     { font-size: $s5; }
    &--sm     { font-size: $s4; }
    &--xs     { font-size: $s3; }
    &--xxs    { font-size: $s2; }
    &--xxxs   { font-size: $s1; }
  }

}

.sub-title {

  // custom content headline
  font-family: $font-title-01;
  font-size: $s4; // default size
  font-weight: 300;
  line-height: 1.2em;

  &--lg     {
    font-size: $s3;
    line-height: 1.4em;
    font-weight: 300; }

  &--sm     { font-size: $s2; }

  @include bp($md){

    font-size: $s6; // default size
    &--lg     {  font-size: $s6; line-height: 1.2em; letter-spacing: .04em;
    //text-transform: uppercase;
 }
    &--sm     {  font-size: $s1; }
  }

}


.core-title {

  // open-sans – core site typeface
  font-family: $font-core;
  font-weight: 300;
  letter-spacing: -.02em;

  &--xxxl   { font-size: $s8; line-height: 1.1; }
  &--xxl    { font-size: $s7; line-height: 1.1; }
  &--xl     { font-size: $s6; line-height: 1.2; }
  &--lg     { font-size: $s5; line-height: 1.25; }
  &--md     { font-size: $s4; line-height: 1.35; }
  &--sm     { font-size: $s3; line-height: 1.35; }
  &--xs     { font-size: $s2; line-height: 1.35; }
  &--xxs    { font-size: $s1; line-height: 1.4; }
  &--xxxs   { font-size: $s0; line-height: 1.45; }
  @include bp($lg){
    &--xxxl   { font-size: $s9; line-height: 1.1; }
    &--xxl    { font-size: $s8; line-height: 1.175; }
    &--xl     { font-size: $s7; line-height: 1.2; }
    &--lg     { font-size: $s6; line-height: 1.2; }
    &--md     { font-size: $s5; line-height: 1.3; }
    &--sm     { font-size: $s4; line-height: 1.3; }
    &--xs     { font-size: $s3; line-height: 1.3; }
    &--xxs    { font-size: $s2; line-height: 1.35; }
    &--xxxs   { font-size: $s1; line-height: 1.4; }
  }

}


.banner-title {

  // custom banner headline
  font-family: $font-title-sc;
  font-weight: 500;
  //text-transform: uppercase;
  //letter-spacing: 1em;
  font-size: $s8;
  line-height: 1em;

  @include bp($sm){ font-size: $s9;  line-height: 1em; }
  @include bp($md){ font-size: $s10;  line-height: 1em; }
  @include bp($lg){ font-size: $s11;  line-height: 1em; }
  @include bp($xl){ font-size: $s12;  line-height: 1em;


  }

}

.serif-title {

  // custom intro headline
  font-family: $font-title-sc;
  font-weight: 300;
  letter-spacing: -.02em;
  //color: $f-blue;
  font-size: $s5;

  &--xl   { font-size: $s7; line-height: 1.1; }
  &--xxl   { font-size: $s7; line-height: 1.1; }

  @include bp($md){ font-size: $s6;

  &--xxl   { font-size: $s8; line-height: 1.1; }
}
  @include bp($xl){ font-size: $s6; letter-spacing: 1px; line-height: 1em;
  &--lg   { font-size: $s7; line-height: 1.2; }
  &--xl   { font-size: $s8; line-height: 1.2; }
  &--xxl   { font-size: $s9; line-height: 1.1; }

  }

}
